   /* iphone适配 */
   padding-top: constant(safe-area-inset-top);/*为导航栏+状态栏的高度 88px*/
   padding-left: constant(safe-area-inset-left);   /*如果未竖屏时为0*/
   padding-right: constant(safe-area-inset-right); /*如果未竖屏时为0*/
   padding-bottom: constant(safe-area-inset-bottom);/*为底下圆弧的高度 34px*/
}
/* flex布局 */
/* 使用注意：
flex下的子元素必须为块级元素，非块级元素在android2.3机器下flex失效;
flex下的子元素宽度和高度不能超过父元素，否则会导致子元素定位错误，例如水平垂直居中;
space-around 不在旧标准属性里 解决方案是使用space-between即justify 加给first-child last-child添加margin */
.flex {/* 定义布局为盒模型 */
   display: -webkit-box;/* OLD - iOS 6-, Safari 3.1-6 */
   display: -webkit-box; /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. *//* NEW - Chrome */
   display: -ms-flexbox;/* TWEENER - IE 10 */
   display: flex;/* NEW, Spec - Opera 12.1, Firefox 20+ */
}
.flex_1{/* 子元素占据剩余的空间 */
   -webkit-box-flex:1;/* OLD - iOS 6-, Safari 3.1-6 *//* NEW - Chrome */
   -ms-flex:1;/* TWEENER - IE 10 */
   flex:1;/* NEW, Spec - Opera 12.1, Firefox 20+ */
}
.flex_v {/* 盒模型垂直布局 */
   -webkit-box-orient: vertical;/* OLD - iOS 6-, Safari 3.1-6 *//* NEW - Chrome */
   -ms-flex-direction: column;/* TWEENER - IE 10 */
   flex-direction: column;/* NEW, Spec - Opera 12.1, Firefox 20+ */
}
.flex_vcenter{/* 子元素垂直居中 */
   -webkit-box-align:center;/* OLD - iOS 6-, Safari 3.1-6 *//* NEW - Chrome */
   -ms-flex-align:center;/* TWEENER - IE 10 */
   align-items:center;/* NEW, Spec - Opera 12.1, Firefox 20+ */
}
.flex_center{/* 子元素水平居中 */
   -webkit-box-pack:center;/* OLD - iOS 6-, Safari 3.1-6 *//* NEW - Chrome */
   -moz-justify-content: center;
   -ms-flex-pack:center;/* TWEENER - IE 10 */
   justify-content:center;/* NEW, Spec - Opera 12.1, Firefox 20+ */
}
.flex_between{/* 子元素两端对齐 */
   -webkit-box-pack:justify;/* OLD - iOS 6-, Safari 3.1-6 *//* NEW - Chrome */
   -ms-flex-pack:justify;/* TWEENER - IE 10 */
   justify-content:space-between;/* NEW, Spec - Opera 12.1, Firefox 20+ */
}
.flex_wrap{
   -ms-flex-wrap: wrap;
   -o-flex-wrap: wrap;
   flex-wrap:wrap;
}
